.combo *,
.combo *::before,
.combo *::after {
	box-sizing: border-box;
}

.combo {
	display: block;
	margin-bottom: 1.5em;
	max-width: 400px;
	position: relative;
}

.combo {
	&::after {
		border-bottom: 2px solid darkcyan;
		border-right: 2px solid darkcyan;
		content: '';
		display: block;
		height: 12px;
		pointer-events: none;
		position: absolute;
		right: 16px;
		top: 50%;
		transform: translate(0, -65%) rotate(45deg);
		width: 12px;
	}
}

.combo-input {
	background-color: #f5f5f5;
	border: 1px solid darkcyan;
	border-radius: 4px;
	display: block;
	font-size: 1em;
	min-height: calc(1.4em + 26px);
	padding: 12px 16px 14px;
	text-align: left;
	width: 100%;
}

.open .combo-input {
	border-radius: 4px 4px 0 0;
}

.combo-input {
	&:focus {
		border-color: darkcyan;
		box-shadow: 0 0 4px 2px darkcyan;
		outline: 4px solid transparent;
	}
}

.combo-label {
	display: block;
	font-size: 20px;
	font-weight: 100;
	margin-bottom: 0.25em;
}

.combo-menu {
	background-color: #f5f5f5;
	border: 1px solid darkcyan;
	border-radius: 0 0 4px 4px;
	display: none;
	max-height: 300px;
	overflow-y: scroll;
	left: 0;
	position: absolute;
	top: 100%;
	width: 100%;
	z-index: 100;

	&::-webkit-scrollbar {
		width: 0.3rem;
		padding: 0.031em;
	}

	&::-webkit-scrollbar-track {
		background: transparent;
	}

	&::-webkit-scrollbar-thumb {
		border: 1px solid gray;
		border-radius: 2px;

		background: {
			color: #006775;
			image: -webkit-linear-gradient(
				45deg,
				#1ab9ce 25%,
				transparent 25%,
				transparent 50%,
				#1ab9ce 50%,
				#1ab9ce 75%,
				transparent 75%,
				transparent
			);
		}
	}

	&::-webkit-scrollbar-thumb:hover {
		background: {
			color: #1ab9ce;
			image: -webkit-linear-gradient(
				45deg,
				#006775 25%,
				transparent 25%,
				transparent 50%,
				#006775 50%,
				#006775 75%,
				transparent 75%,
				transparent
			);
		}
	}
}

.open .combo-menu {
	margin-top: 0.5rem;
	display: block;
	box-shadow: 0 0 4px 2px darkcyan;
}

.combo-option {
	padding: 10px 12px 12px;
}

.combo-option:hover {
	background-color: rgba(0, 139, 139, 0.295);
}

.combo-option {
	&.option-current {
		outline: 1px solid darkcyan;
		outline-offset: -3px;
	}
}

.combo-option[aria-selected='true'] {
	padding-right: 0.9rem;
	position: relative;
}

.combo-option[aria-selected='true']::after {
	border-bottom: 2px solid darkcyan;
	border-right: 2px solid darkcyan;
	content: '';
	height: 16px;
	position: absolute;
	right: 15px;
	top: 50%;
	transform: translate(0, -50%) rotate(45deg);
	width: 8px;
}
